import React from 'react'
import { Col, Container, Row, ListGroupItem, ListGroup, Card,Form, FormControl } from 'react-bootstrap';
import { EP_ARCHIVOS_PUBLICOS_AMIGOS } from '../../../../Constants';
import FileComponent from '../foldercontent/components/file/File';
import FriendFiles from './FriendFiles';

class PublicFiles extends React.Component{
    
    constructor(props){
        super(props)
        this.state = {
            amigos:[],
            buscarArchivos:""
        }
        this.amigosyarchivos()
        this.refFriendsFiles = []
    }

    amigosyarchivos(){
        fetch(
            `${EP_ARCHIVOS_PUBLICOS_AMIGOS}`,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        Usuario:localStorage['usuario']
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                if (res.Error !== 0){
                    alert(res.Mensaje)
                    return
                }
                this.setState({amigos:res.Lista})
            }
        )
    }

    render(){
        return(
            <Container className="PublicFilesComponent">
                <Row>
                    <Col>
                        <h3>Archivos públicos de mis amigos</h3>
                    </Col>
                    <Col md="4" sm="12">
                        <Form className="d-flex">
                            <FormControl
                                type="search"
                                placeholder="Buscar archivo" 
                                className="mr-2"
                                aria-label="Search"
                                value={this.state.buscarArchivos}
                                onChange={e => this.setState({buscarArchivos:e.target.value})}
                            />
                        </Form>
                    </Col>
                </Row>
                <Row>
                    {/* <Col></Col> */}
                    <Col>
                        <Container>
                            <ListGroup>
                                <ListGroupItem>
                                        {
                                            this.state.amigos.map(
                                                a => {
                                                    //console.log(a.Documentos.filter(o => o.Nombre.includes(this.state.buscarArchivos)));
                                                    return (
                                                        <FriendFiles key={a.Username} 
                                                            nombre={a.Nombre}
                                                            usuario={a.Username}
                                                            archivos={a.Documentos.filter(o => o.Nombre.includes(this.state.buscarArchivos))}
                                                            buscarArchivos={this.state.buscarArchivos}
                                                        />
                                                    )
                                                }
                                            )
                                        }
                                </ListGroupItem>
                            </ListGroup>
                        </Container>
                    </Col>
                    {/* <Col></Col> */}
                </Row>
            </Container>
        )
    }
}

export default PublicFiles;