import React from "react";
import { Col, Container, Row } from "react-bootstrap";
import { EP_SOLICITUD_AMISTAD } from "../../../../Constants";
import Friend from "../friends/Fiend";

export default class FriendRequest extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            friendRequests:[]
        }
    }

    componentDidMount(){
        this.getFriendRequest()
    }

    getFriendRequest(){
        fetch(
            EP_SOLICITUD_AMISTAD,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        NoUsuario:localStorage['usuario']
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                console.log(res);
                this.setState({friendRequests:res.Gente})
            }
        )
    }

    onFriendUpdate(obj){

    }

    render(){
        return (
            <Container>
                <Row className="justify-content-center">
                    <Col md="8" xs="10">
                        <Row>
                            <h2>Solicitudes de amistad</h2>
                        </Row>
                        <Row>
                            {
                                this.state.friendRequests
                                //.filter(a => a.Nombre.includes(this.state.buscarUsuario) )
                                .map(
                                    o=>{
                                        return <Friend 
                                            key={o.NoUsuario}
                                            nousuario={o.NoUsuario}
                                            nombre={o.Nombre}
                                            foto={o.Foto}
                                            modobot={o.ModoBot}
                                            accion="Aceptar|Rechazar"
                                            onUpdate={this.onFriendUpdate.bind(this)}
                                        />
                                    }
                                )
                            }
                        </Row>
                    </Col>
                </Row>
            </Container>
        )
    }
}