import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import './Nav.css'
import { Nav, Navbar, Container, NavDropdown } from 'react-bootstrap'
import Side from "../sidebar/Navbar";

class NavComponent extends React.Component {
    render(){
        return (
            <Navbar className="yomero" bg="dark" variant="dark" expand="lg">
                <Container>
                    <Side/>
                    <a>---</a>
                    <Nav className="me-auto">
                        <Nav.Link href="#">Bienvenido {localStorage['nombre']}</Nav.Link>
                    </Nav>
                    <Navbar.Brand href="#"> 
                        SemiCloud
                    </Navbar.Brand>
                </Container>
            </Navbar>
        );
    }
}

export default NavComponent;