import React from "react";
import {Link} from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import {Container,Row, Col, Form, Button} from 'react-bootstrap'
import { EP_LOGIN_FACIAL, EP_LOGIN_NORMAL, EP_VALIDAR_USUARIO } from "../../Constants";
import Encriptador from "../Cripter";
import { AiFillCamera } from "react-icons/ai";
import ModalOpciones from "../home/components/foldercontent/components/modal/Modal";
import CameraComponent from "./components/camera/camera";


class Login extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            usuario:"",
            contrasena:"",
            foto:"",
            codigo:""
        }
        this.encriptador = new Encriptador()
        if (localStorage['usuario'] && localStorage['usuario'] != '') {
            this.props.history.push('/inicio/publicaciones')
        }
        this.refCamera = React.createRef()
        this.refModalCamera = React.createRef()
        this.refModalVerification = React.createRef()
    }

    async login(event){
        event.preventDefault()
        var pathRequest = this.state.foto == ''? EP_LOGIN_NORMAL: EP_LOGIN_FACIAL
        fetch(
            `${pathRequest}`,
            {
                method: 'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UserName:this.state.usuario,
                        Password:this.state.contrasena,
                        Imagen:this.state.foto
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
                if(res.Error === 0){
                    console.log(res.NoUsuario);
                    localStorage.setItem(`usuario`,res.NoUsuario)
                    this.props.history.push("/inicio/publicaciones")
                }
            }
        )
    }

    async facialLogin(event){
        event.preventDefault()
        const strImage = this.refCamera.current.takePhoto(event)
        this.setState({foto:strImage})
        this.login(event)
    }

    async validateUser(event){
        event.preventDefault()
        fetch(
            EP_VALIDAR_USUARIO,
            {
                method: 'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UserName:this.state.usuario,
                        Codigo:this.state.codigo
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
                console.log(res);
                this.refModalVerification.current.handleClose()
            }
        )
    }

    showModalFacialLogin(event){
        event.preventDefault()
        this.refModalCamera.current.setModal("Reconocimiento facial","")
        this.refModalCamera.current.handleShow()
    }

    showModalVerification(event){
        event.preventDefault()
        this.refModalVerification.current.setModal("Validar usuario","")
        this.refModalVerification.current.handleShow()
    }

    render(){
        return(
            <div className="login-component">
                <Container className="col align-self-center" >
                    <br></br>
                    <br></br>
                    <br></br>
                    <Row className="">
                        <Col sm="2" md="4"></Col>
                        <Col sm="8" md="4">
                            <Form onSubmit={this.login.bind(this)}>
                                <h3>Iniciar sesión</h3>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>Dirección de correo</Form.Label>
                                    <Form.Control type="text" placeholder="Ingresa tu usuario o correo" required
                                        value={this.state.usuario}
                                        onChange={e => this.setState({usuario:e.target.value})}
                                    />
                                    <Form.Text className="text-muted">
                                        No compartiremos tu correo electrónico con nadie.
                                    </Form.Text>
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formBasicPassword">
                                    <Form.Label>Contraseña</Form.Label>
                                    <Form.Control type="password" placeholder="Ingresa tu contraseña" 
                                        value={this.state.contrasena}
                                        onChange={e => this.setState({contrasena:e.target.value})}
                                    />
                                </Form.Group>
                                <Form.Group className="mb-3">
                                    <Button variant="primary" type="submit">
                                        Ingresar
                                    </Button>
                                </Form.Group>
                            </Form>
                            <div className="mb-3">
                                <Link to= {{ pathname: "/registro"}}>¿No tienes cuenta? Regístrate</Link>
                            </div>
                            <div className="mb-3">
                                <Link to="#" onClick={this.showModalFacialLogin.bind(this)}>Iniciar sesión con reconocimiento facial <AiFillCamera></AiFillCamera> </Link>
                            </div>
                            <div className="mb-3">
                                <Link to="#" onClick={this.showModalVerification.bind(this)}>Validar mi usuario</Link>
                            </div>
                        </Col>
                        <Col sm="8" md="4"></Col>
                    </Row>
                    <Row>
                        <ModalOpciones size="md" ref={this.refModalCamera}>
                            <CameraComponent ref={this.refCamera}/>
                            <Row>
                                <Col></Col>
                                <Col>
                                    <br/>
                                    <div className="d-grid">
                                    <Button onClick={this.facialLogin.bind(this)} size="lg" >Ingresar</Button>
                                    </div>
                                </Col>
                                <Col></Col>
                            </Row>
                        </ModalOpciones>
                        <ModalOpciones size="md" ref={this.refModalVerification}>
                            <Row className="justify-content-center">
                                <Col className="justify-content-center">
                                    <Form>
                                        <Form.Group className="mb-3" controlId="formBasicEmail">
                                            <Form.Label>Ingresa el usuario que deseas validar</Form.Label>
                                            <Form.Control type="text" placeholder="Ingresa tu usuario" required
                                                value={this.state.usuario}
                                                onChange={e => this.setState({usuario:e.target.value})}
                                            />
                                        </Form.Group>
                                        <Form.Group className="mb-3">
                                            <Form.Label>Ingresar código de verificación que llegó a tu correo electrónico</Form.Label>
                                            <Form.Control type="text"
                                                placeholder="Código de verificación"
                                                value={this.state.codigo}
                                                onChange={(e)=> this.setState({codigo:e.target.value})}
                                            ></Form.Control>
                                        </Form.Group>
                                        <Form.Group className="d-grid mb-3">
                                            <Button size="lg" onClick={this.validateUser.bind(this)}>Verificar mi usuario</Button>
                                        </Form.Group>
                                    </Form>
                                </Col>
                            </Row>
                        </ModalOpciones>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default Login;