import {Switch, Route} from 'react-router-dom'
import React from 'react';
import Login from './pages/login/Login'
import Register from './pages/register/Register'
import Home from './pages/home/Home'
import CameraComponent from './pages/login/components/camera/camera';
import UpdateUser from './pages/home/components/updateuser/UpdateUser';
import NoFriends from './pages/home/components/nofriends/NoFriends';
import Chat from './pages/home/components/chat/Chat';
import ChatList from './pages/home/components/chat/ChatList';
import Publication from './pages/home/components/publications/Publication';
import Publications from './pages/home/components/publications/Publications';
import Mensaje from './pages/home/components/chat/Mensaje';

const Routes = () => {
    return (
        <Switch>
            <Route exact path="/"           component={Login}></Route>
            <Route exact path="/registro"   component={Register}></Route>
            <Route       path="/inicio"     component={Home}></Route>
            <Route       path="/test"       component={Mensaje}></Route>
        </Switch>
    );
}

export default Routes;