
import React from "react";
import { Col, Container, ListGroup, Row, Button, Form, ListGroupItem } from "react-bootstrap";
import ModalOpciones from "../foldercontent/components/modal/Modal";
import {AiOutlineReload, AiOutlineSend } from "react-icons/ai"
import "./Chat.css"
import { EP_ENVIAR_MENSAJE, EP_VER_ID_CHAT, EP_VER_MENSAJES } from "../../../../Constants";
import Mensaje from "./Mensaje";

export default class Chat extends React.Component {
    constructor(props){
        super(props)
        console.log(props);

        this.state = {
            Mensajes:[],
            Nombre:props.Nombre,
            NoUsuario:props.NoUsuario,
            ModoBot:props.ModoBot,
            Foto:props.Foto,
            Chat:"",
            MensajeEnviar:""
        }
        
        this.refModalChat = React.createRef()
    }

    componentDidMount(){
        this.getChat()
    }

    showModal(event){
        event.preventDefault()
        this.refModalChat.current.setModal(this.state.Nombre,"")
        this.refModalChat.current.handleShow()
        console.log(localStorage['usuario']);
        console.log(this.props.NoUsuario);
    }

    getChat(){
        fetch(
            EP_VER_ID_CHAT,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UsuarioYo:localStorage['usuario'],
                        UsuarioDestino:this.props.NoUsuario
                    }
                )
            }
        )
        .then(res => res.json()).then(
            res=>
            {
                console.log(res);
                this.setState({Chat:1})
                this.getMensajes()
                //this.setState({Chat:res.NoChat})
            }
        )
    }

    getMensajes(){
        fetch(
            EP_VER_MENSAJES,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        IdChat:this.state.Chat
                    }
                )
            }
        )
        .then(res => res.json()).then(
            res=>
            { 
                this.setState(
                    {
                        Mensajes:res.Mensajes.sort(
                            (a,b) => (a.NoMensaje > b.NoMensaje) ? 1 : ((b.NoMensaje > a.NoMensaje) ? -1 : 0)
                        )
                    }
                )
                console.log(this.state.Mensajes);
                //console.log(document.getElementById("soyeldiv"));
            }
        )
    }

    postMensaje(){
        fetch(
            EP_ENVIAR_MENSAJE,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UsuarioYo:localStorage['usuario'],
                        UsuarioDestino:this.state.NoUsuario,
                        Mensaje:this.state.MensajeEnviar
                    }
                )
            }
        )
        .then(res => res.json()).then(
            res=>
            {
                console.log(res);
                this.getMensajes()
                this.setState({MensajeEnviar:''})
            }
        )
    }

    render(){
        return (
            <Container>
                <ModalOpciones ref={this.refModalChat} scrollable={false}>
                    <Row className="justify-content-end mb-2">
                        <Col className="col-2">
                            <Button onClick={this.getMensajes.bind(this)}><AiOutlineReload></AiOutlineReload></Button>
                        </Col>
                    </Row>
                    <Row className="mb-3">
                        <ListGroup className="vertical-scrollable" id="lista-mensajes">
                            <ListGroupItem className="mensajes"> 
                                {
                                    this.state.Mensajes.map(
                                        (o,index) => {
                                            return <Mensaje
                                                key={index} 
                                                Mensaje={o.Mensaje}
                                                Foto={o.Foto} 
                                                NoUsuario={o.NoUsuario}
                                            />
                                        }
                                    )
                                }
                            </ListGroupItem>
                            <div id="soyeldiv"></div> 
                        </ListGroup>
                    </Row>
                    <Row>
                        <Col md="10" sm="10" xs="10">
                            <Form>
                                <Form.Control 
                                    type="text"
                                    value={this.state.MensajeEnviar}
                                    onChange={e => this.setState({MensajeEnviar:e.target.value})}
                                ></Form.Control>
                            </Form>
                        </Col>
                        <Col md="2" sm="2" xs="2">
                            <Button variant="primary"
                                onClick={this.postMensaje.bind(this)}
                            ><AiOutlineSend></AiOutlineSend></Button>
                        </Col>
                    </Row>
                </ModalOpciones>
                {/* <Button onClick={this.showModal.bind(this)}>click</Button> */}
            </Container>
        )
    }
}