import React from "react";
import { Col, Container, Form, FormControl, Row } from "react-bootstrap";
import { EP_NO_AMIGOS } from "../../../../Constants";
import Friend from "../friends/Fiend";

export default class NoFriends extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            noFriends: [],
            buscarUsuario:""
        }
    }

    componentDidMount(){
        this.getNoFriends()
    }

    getNoFriends(){
        fetch(
            EP_NO_AMIGOS,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        NoUsuario:localStorage['usuario']
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                console.log(res);
                this.setState({noFriends:res.Gente})
            }
        )
    }

    onFriendUpdate(){
        console.log('fiend updated');
    }

    
    render(){
        return (
            <Container>
                <Row className="mb-3 justify-content-center">
                    <Col sm="8" md="8" xs="12">
                        <Row>
                            <Col>
                                <h2>Usuarios</h2>
                            </Col>
                            <Col>
                                <Form className="d-flex">
                                    <FormControl
                                        type="search"
                                        placeholder="Buscar usuario"
                                        className="mr-2"
                                        aria-label="Search"
                                        value={this.state.buscarUsuario}
                                        onChange={e => this.setState({buscarUsuario:e.target.value})}
                                    />
                                </Form>
                            </Col>
                        </Row>
                        <Row>
                            {
                                this.state.noFriends
                                .filter(a => a.Nombre.includes(this.state.buscarUsuario) )
                                .map(
                                    o=>{
                                        return <Friend 
                                            key={o.NoUsuario}
                                            nousuario={o.NoUsuario}
                                            nombre={o.Nombre}
                                            foto={o.Foto}
                                            modobot={o.ModoBot}
                                            accion="Agregar amigo"
                                            onUpdate={this.onFriendUpdate.bind(this)}
                                        />
                                    }
                                )
                            }
                        </Row>
                    </Col>
                </Row>
            </Container>
        )
    }
}