import React from "react";
import { Col, Container, Form, FormControl, Row } from "react-bootstrap";
import { EP_VER_PUBLICACIONES } from "../../../../Constants";
import Publication from "./Publication";
import Publisher from "./Publisher";

export default class Publications extends React.Component{
    constructor(props){
        super(props)
        this.state={
            publications:[],
            buscarTag:""
        }
    }

    componentDidMount(){
        this.getPublications()
    }

    getPublications(){
        fetch(
            EP_VER_PUBLICACIONES,
            {
                method:'GET',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
            }
        )
        .then(res => res.json()).then(
            res=>
            {
                console.log(res);
                this.setState({publications:res.Publicaciones})
            }
        )
    }

    onPublisherUpdated(e){
        console.log('publsher dice que se actualizó');
        this.getPublications()
    }

    render(){
        return (
            <Container>
                <Row>
                    <Publisher onUpdate={this.onPublisherUpdated.bind(this)} ></Publisher>
                </Row>
                <Row className="justify-content-end">
                    <Col md="6" sm="6">
                        <Form className="d-flex mb-5">
                            <FormControl
                                type="search"
                                placeholder="Buscar etiquetas"
                                className="mr-2"
                                aria-label="Search"
                                value={this.state.buscarTag}
                                onChange={e => this.setState({buscarTag:e.target.value})}
                            />
                        </Form>
                    </Col>
                </Row>
                <Row>
                    {
                        this.state.publications
                        .filter(o => o.Etiquetas.toLowerCase().includes(this.state.buscarTag.toLowerCase()))
                        .map(
                            e => <Publication
                            key={e.NoPublicacion}
                            NoPublicacion={e.NoPublicacion}
                            Texto={e.Texto}
                            Imagen={e.Imagen} 
                            Etiquetas={e.Etiquetas}
                            Fecha={e.Fecha}
                            NoUsuario={e.NoUsuario}
                            Nombre={e.Nombre}
                            Foto={e.Foto}
                            />
                        )
                    }
                </Row>
            </Container>
        )
    }
}