import React from "react";
import { Col, Card, Container, Row, Button, Form} from 'react-bootstrap'
import { Link } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css'
import './File.css'
import ModalOpciones from "../modal/Modal";
import View from "./components/View";
import Delete from "./components/Delete";
import Eddit from "./components/Eddit";

import { Route, Switch } from "react-router";
import { AiFillEdit, AiFillFileExclamation, AiFillFileImage, AiFillFilePdf, AiFillFileText } from "react-icons/ai";

class FileComponent extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            modalToShow:1,
            fileName:props.fileName,
            id:props.idf,
            unicName:props.unicName,
            format:this.getFormat(props.format),
            icon:this.getIcon(props.format),
            visible:props.visible,
            editable:props.editable,
            sizeModal:"md"
        }
        this.refModalOptions = React.createRef()
        this.refModalOption = React.createRef()
        this.refModalView= React.createRef()
    }

    getFormat(id){
        return id == 1 ? 'pdf' : id == 2 ? 'txt' : id==3? 'png' : 'jpeg'
    }

    getIcon(id){
        return id == 1 ? <AiFillFilePdf/> : 
        id == 2 ? <AiFillFileText/> : 
        id==3? <AiFillFileImage/> : <AiFillFileImage/>
    }

    showOptions(event){
        event.preventDefault()
        this.refModalOptions.current.setModal("Opciones","¿Qué deseas hacer?")
        this.refModalOptions.current.handleShow()
    }

    showOption(event){
        event.preventDefault()
        if (event.target.innerText === "Ver archivo") {
            this.setState({modalToShow:1})
            this.refModalView.current.setModal(event.target.innerText,"")
            this.refModalView.current.handleShow()
            return 
        }else if (event.target.innerText === "Editar archivo"){
            this.setState({modalToShow:2})
        }else{
            this.setState({modalToShow:3})
        }

        this.refModalOption.current.setModal(event.target.innerText,"")
        this.refModalOption.current.handleShow()
    }

    showOptionSelected(nameFile){
        return this.state.modalToShow == 1? 
            <View fileName={this.state.fileName} id={this.state.id} unicName={this.state.unicName}/>: 
            this.state.modalToShow == 2? 
            <Eddit fileName={this.state.fileName} id={this.state.id} visible={this.state.visible}/>:
            <Delete fileName={this.state.fileName + '.' + this.state.format} id={this.state.id}/> 
    }

    showEditionOpions(){
        let contenido = (<h1>camino</h1>)
        if (this.state.editable){
            contenido = (<h1>no camino</h1>)
        }
        return this.state.editable ? 
        <>
        <Link to="/inicio/archivos/editar" className="btn btn-light btn-block"
            onClick={this.showOption.bind(this)}
        >
            Editar archivo
        </Link>
        <Link to="/inicio/archivos/eliminar" className="btn btn-light btn-block"
            onClick={this.showOption.bind(this)}
        >
            Eliminar archivo
        </Link>
        </>:<></>
    }

    render(){
        return (
            <Col auto>
                <div onClickCapture={this.showOptions.bind(this)}>
                <Card style={{ width: '7rem', margin:'5%' }} >
                    {/* <Card.Img variant="top" src="holder.js/100px180" /> */}
                    <h1>{this.state.icon}</h1>
                    <Card.Body>
                        <Card.Text><a>{this.state.fileName}.{this.state.format}</a></Card.Text>
                    </Card.Body>
                </Card>
                </div>
                <ModalOpciones size="sm" ref={this.refModalOptions}>
                    <div className="d-grid gap-2">
                        <Link to="/inicio/archivos/ver" className="btn btn-light btn-block"
                            onClick={this.showOption.bind(this)}
                        >
                            Ver archivo
                        </Link>
                        {
                            this.showEditionOpions()
                        }
                    </div>
                </ModalOpciones>
                <ModalOpciones ref={this.refModalOption} size="md">
                    {
                        this.showOptionSelected()
                    }
                </ModalOpciones>
                <ModalOpciones ref={this.refModalView} fullscreen={true}>
                    {
                        this.showOptionSelected()
                    }
                </ModalOpciones>
            </Col>
        )
    }
}

export default FileComponent;
