import React from 'react';
import FileUploader from './images/FileUploader';
import "bootstrap/dist/css/bootstrap.min.css"
import { Col, Container, Row, Form, Button } from 'react-bootstrap';
import { EP_REGISTRO } from '../../Constants';
import Encriptador from '../Cripter';
import { AiFillCamera } from 'react-icons/ai';
import ModalOpciones from '../home/components/foldercontent/components/modal/Modal';
import CameraComponent from '../login/components/camera/camera';
//import Button from '@restart/ui/esm/Button';

class Register extends React.Component {

    constructor(props){
        super(props)
        this.state = {
            nombre:"",
            correo:"",
            usuario:"",
            contrasena:"",
            confirmContrasena:"",
            foto:"", 
            formato:""
        }
        this.refFile = React.createRef() 
        this.encriptado = new Encriptador()

        this.refModalCamera = React.createRef()
        this.refCamera = React.createRef()
    }

    async registrar(event){
        event.preventDefault()
        console.log('doing register');
        if (this.state.contrasena !== this.state.confirmContrasena){
            alert("Contraseñas no coinciden")
            return 
        }

        if (this.state.foto == ''){
            this.setState({foto:this.refFile.current.getImageSrc()})
        }

        console.log(this.state.foto);
        fetch(`${EP_REGISTRO}`,{
            method: 'POST',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
            body:JSON.stringify(
                {
                    Correo:this.state.correo,
                    Password:this.state.contrasena,
                    UserName:this.state.usuario,
                    Nombre:this.state.nombre,
                    Foto:this.state.foto
                }
            )
        }).then(res=>res.json()).then(
            res=>{
                console.log(res);
                alert(res.Mensaje)
                if (res.Error === 0){
                    // registro exitoso, redirige a login
                    this.props.history.push('/')
                }
            }
        )
    }

    showModalCamera(event){
        event.preventDefault()
        this.refModalCamera.current.setModal("Reconocimiento facial","")
        this.refModalCamera.current.handleShow()
    }

    takePhoto(event){
        event.preventDefault()
        const strImg = this.refCamera.current.takePhoto(event)
        this.setState({foto:strImg})
        this.refFile.current.setImageSrc(strImg)
        this.refModalCamera.current.handleClose()
    }


    render(){
        return (
            <div className="register-component">
                <Container>
                    <br></br><br></br><br></br>
                    <Row>
                        <Col></Col>
                        <Col md="5" sm="8">
                            <Form onSubmit={this.registrar.bind(this)}>
                                <h3>Registrar usuario nuevo</h3>
                                <Form.Group className="">
                                    <Form.Label>Nombre</Form.Label>
                                    <Form.Control require type="text" placeholder="Ingresa tu nombre" 
                                        value={this.state.nombre}
                                        onChange={e =>this.setState({nombre:e.target.value})}
                                    />
                                </Form.Group>
                                <br></br>
                                <Form.Group className="">
                                    <Form.Label>Correo electrónico</Form.Label>
                                    <Form.Control type="email" placeholder="Ingresa tu correo electrónico" 
                                        value = {this.state.correo}
                                        onChange={e => this.setState({correo:e.target.value})}
                                    />
                                </Form.Group>
                                <br></br>
                                <Form.Group className="">
                                    <Form.Label>Nombre de usuario</Form.Label>
                                    <Form.Control type="text" placeholder="Ingresa el nombre de usuario" 
                                        value = {this.state.usuario}
                                        onChange={e => this.setState({usuario:e.target.value})}
                                    />
                                </Form.Group>
                                <br></br>
                                <Form.Group className="">
                                    <Form.Label>Contraseña</Form.Label>
                                    <Form.Control type="password" placeholder="Ingresa tu contraseña" 
                                        value = {this.state.contrasena}
                                        onChange={e => this.setState({contrasena:e.target.value})}
                                    />
                                </Form.Group>
                                <br></br>
                                <Form.Group className="">
                                    <Form.Label>Confirmar contraseña</Form.Label>
                                    <Form.Control type="password" placeholder="Confirma tu contraseña" 
                                        value = {this.state.confirmContrasena}
                                        onChange={
                                            e => this.setState({confirmContrasena:e.target.value})
                                        }
                                    />
                                </Form.Group>
                                <br></br>
                                <Form.Group className="">
                                    <Form.Label>Subir una fotografía</Form.Label>
                                    <Row>
                                        <Col xs={12} md={8}>
                                            <FileUploader ref={this.refFile} ></FileUploader>
                                        </Col>
                                        <Col xs={12} md={4}>
                                            <Button onClick={this.showModalCamera.bind(this)} ><AiFillCamera></AiFillCamera></Button>
                                        </Col>
                                    </Row>
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>
                                        Al terminar su registro reconoce que acepta las polítitcas del la aplicación.
                                    </Form.Label>
                                    <Button variant="primary" type="submit">
                                        Terminar registro
                                    </Button>
                                </Form.Group>
                                <br></br>
                            </Form>
                        </Col>
                        <Col></Col>
                    </Row>
                    <Row>
                        <ModalOpciones size="md" ref={this.refModalCamera}>
                            <CameraComponent ref={this.refCamera}/>
                            <Row>
                                <Col></Col>
                                <Col>
                                    <br/>
                                    <div className="d-grid">
                                    <Button onClick={this.takePhoto.bind(this)} size="lg" >Tomar fotografía</Button>
                                    </div>
                                </Col>
                                <Col></Col>
                            </Row>
                        </ModalOpciones>
                    </Row>
                </Container>
            </div>
        );
    }
}

export default Register;