import cryptoJS  from 'crypto-js'

export default class Encriptador {
    
    encriptar(cadena){
        let key = cryptoJS.enc.Utf8.parse(cadena)
        let iv = cryptoJS.enc.Utf8.parse(cadena)
        var encripted = cryptoJS.AES.encrypt(
            cryptoJS.enc.Utf8.parse('patito'),
            key, {keySize:128/8,iv:iv,mode:cryptoJS.mode.CBC,padding:cryptoJS.pad.Pkcs7}
        )
        return encripted.toString()
    }
}