import React from "react";
import { Container, Row,Form,InputGroup,Button, Col } from "react-bootstrap";
import { EP_CARGAR_ARCHIVO, EP_LOGIN } from "../../../../Constants";
import Encriptador from "../../../Cripter";
import GeneralFile from "./GeneralFile";


class UpFile extends React.Component{

    constructor(props){
        super(props)
        this.state={
            contrasena:"",
            nombre:"",
            archivo64:"",
            extensionArchivo:"",
            esPrivado:true
        }
        this.refGeneralFile = React.createRef()
        this.encriptado = new Encriptador()
    }

    archivoCargado(e){
        console.log(e);
        this.setState({extensionArchivo:e.format})
        this.setState({archivo64:e.file64})
        
        if (this.state.nombre === ''){
            this.setState({nombre:e.fileName.split('.')[0]})
        }
    }

    cargarArchivo(event){
        event.preventDefault()

        // validar que se haya cargado un arhcivo
        if (this.state.extensionArchivo === ''){
            alert('Selecciona un archivo para cargar')
            return
        }

        // completar datos
        let usuario = localStorage['usuario'];
        let visibilidad = this.state.esPrivado ? 0 : 1; 
        let tipo = this.state.extensionArchivo.toUpperCase() === 'PDF' ?
            1: this.state.extensionArchivo.toUpperCase() === 'TXT' ||
                this.state.extensionArchivo.toUpperCase() === 'PLAIN' ?
            2: this.state.extensionArchivo.toUpperCase() === 'PNG' ?
            3: this.state.extensionArchivo.toUpperCase() === 'JPEG' || 
                this.state.extensionArchivo.toUpperCase() === 'JPG' ?
            4: 0
        ;
        
        if (tipo === 0){
            alert('solamente puedes cargar archivos PDF TXT PNG o JPEG|JPG');
            return;
        }
        
        fetch(
            `${EP_LOGIN}`,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UserName: usuario,
                        Password:this.encriptado.encriptar(this.state.contrasena)
                    }
                )
            }
        ).then(res=>res.json()).then(
            res => {
                // si hay error, salir
                if (res.Loged !== 1) {
                    alert('Contraseña incorrecta')
                    return
                }

                //contraseña correcta, cargar archivo
                fetch(
                    `${EP_CARGAR_ARCHIVO}`,
                    {
                        method:'POST',
                        mode:'cors',
                        headers:{'Content-Type':'application/json'},
                        body:JSON.stringify(
                            {
                                UserName: usuario,
                                Nombre: this.state.nombre,
                                Tipo: tipo,
                                Visibilidad:visibilidad,
                                Archivo:this.state.archivo64,
                                Password:this.state.contrasena
                            }
                        )
                    }
                ).then(res => res.json()).then(
                    res => {
                        console.log(res);
                        alert(res.Mensaje)
                        if (res.Error === 0 ){
                            // si el archivo fue cargado con éxito
                            // redirigir a carpeta de inicio
                            //this.props.history.push('/inicio/archivos')
                            this.setState({contrasena:""})
                            this.setState({nombre:""})
                            this.setState({archivo64:""})
                            this.setState({extensionArchivo:""})
                            this.setState({esPrivado:true})
                            alert('Ya puedes cargar más archivos :)')
                        }
                    }
                )
            }
        );

    }

    render(){
        return (
            <Container>
                <Row>
                    <Col className="mb-4">
                        <h1>Cargar un archivo nuevo</h1>
                    </Col>
                </Row>
                <Row>
                    <Col sm="12" md="4"></Col>
                    <Col sm="12" md="4">
                        <Form onSubmit={this.cargarArchivo.bind(this)}>
                            <Form.Group className="mb-3">
                                <GeneralFile ref={this.refGeneralFile}
                                    onUpdate={this.archivoCargado.bind(this)}
                                />
                            </Form.Group>
                            <Form.Group className="mb-3"  >
                                <Form.Label as="legend" column>
                                    <strong>Visibilidad</strong>
                                </Form.Label>
                                <Form.Check
                                    required
                                    type="radio"
                                    label="Privado"
                                    name="formVisibilidad"
                                    checked={this.state.esPrivado}
                                    onChange={e=> {this.setState({esPrivado:true})}}
                                />
                                <Form.Check
                                    required
                                    type="radio"
                                    label="Público"
                                    name="formVisibilidad"
                                    checked={!this.state.esPrivado}
                                    onChange={e=> {this.setState({esPrivado:false})}}
                                />
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label as="legend">
                                    Nombre de archivo
                                </Form.Label>
                                <Row>
                                    <Col md="10" sm="10">
                                        <Form.Control type="text" placeholder="Ingresa el nombre del archivo"
                                            value={this.state.nombre}
                                            onChange={(e)=> this.setState({nombre:e.target.value})}
                                        />
                                    </Col>
                                    <Col md="2" sm="2">
                                        <Form.Label as="legend">.{this.state.extensionArchivo}</Form.Label>
                                    </Col>
                                </Row>
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label as="legend">
                                    Confirma tu contraseña
                                </Form.Label>
                                <Form.Control type="password" placeholder="Ingresa la contraseña"
                                    value={this.state.contrasena}
                                    onChange={(e)=>this.setState({contrasena:e.target.value})}
                                />
                            </Form.Group>
                            <br></br>
                            <InputGroup className="d-grid">
                                <Button size="lg" type="submit">
                                    Cargar archivo
                                </Button>
                            </InputGroup>
                        </Form>
                    </Col>
                    <Col sm="12" md="4"></Col>
                </Row>
            </Container>
        )
    }
}

export default UpFile