import React from "react";
import {Switch, Route} from 'react-router-dom'
import "bootstrap/dist/css/bootstrap.min.css"
import "./Home.css"
import NavComponent from "./components/nav/Nav"
import Footer from "./components/footer/Footer"
import FolderContent from "./components/foldercontent/FolderContent"
import { Container, Row, Col } from "react-bootstrap"
import UpFile from "./components/upfile/Upfile";
import Friends from "./components/friends/friends";
import PublicFiles from "./components/publics/PublicFiles";
import UpdateUser from "./components/updateuser/UpdateUser";
import NoFriends from "./components/nofriends/NoFriends";
import ChatList from "./components/chat/ChatList";
import { EP_MIS_DATOS } from "../../Constants";
import FriendRequest from "./components/friendrequest/FriendRequest";
import Publications from "./components/publications/Publications";

class Home extends React.Component{

    constructor(props){
        super(props)
        if (!localStorage['usuario']){
            alert('debes loguarte')
            this.props.history.push('/')
        }
    }

    componentDidMount(){
        this.getUserData()
    }

    bot(d,m ){
        var kommunicateSettings = {
            "appId":"d68289ec0383cac7184a60f1a6fe2ee7",
            "popupWidget":true,
            "automaticChatOpenOnNavigation":true
        };
        var s = document.createElement("script"); 
        s.type = "text/javascript"; 
        s.async = true;
        s.src = "https://widget.kommunicate.io/v2/kommunicate.app";
        var h = document.getElementsByTagName("head")[0]; 
        h.appendChild(s);
        window.kommunicate = m; 
        m._globals = kommunicateSettings;
    }

    getUserData(){
        fetch(
            EP_MIS_DATOS,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        NoUsuario:localStorage['usuario']
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                console.log(res);
                localStorage['nombreusuario'] = res.Datos.UserName
                localStorage['nombre']          = res.Datos.Nombre
                localStorage['correo']        = res.Datos.Correo
                localStorage['foto']          = res.Datos.Foto
                localStorage['modobot']       = res.Datos.ModoBot    
                
                if (res.Datos.ModoBot === 1) {
                    this.bot(document, window.kommunicate || {})
                }
            }
        )
    }

    irArchivos(event){
        this.props.history.push('/inicio/archivos')
    }

    render(){
        return(
            <div className="home-component">
                <NavComponent></NavComponent>
                <br></br>
                <Container>
                    <Row>
                        <Col>
                            <Switch>
                                <Route
                                    exact
                                    path="/inicio/amigos"
                                    component={
                                        () => <Friends />}
                                >
                                </Route>
                                <Route
                                    exact
                                    path="/inicio/solicitudes"
                                    component={()=><FriendRequest/>}
                                ></Route>
                                <Route
                                    exact
                                    path="/inicio/modificardatos"
                                    component={()=> <UpdateUser/>}
                                ></Route>
                                <Route
                                    exact
                                    path="/inicio/chat"
                                    component={()=> <ChatList/>}
                                ></Route>
                                <Route
                                    exact
                                    path="/inicio/agregaramigos"
                                    component={()=> <NoFriends/>}
                                ></Route>
                                <Route
                                    exact
                                    path="/inicio/publicaciones"
                                    component={()=> <Publications/>}
                                ></Route>
                            </Switch>
                        </Col>
                    </Row>
                    <br></br>
                </Container>
                {/* <Footer></Footer> */}
            </div>
        );
    }
}

export default Home;