import React from "react";
import { Col, Container, Form, Button, FormGroup, Row, Image } from "react-bootstrap";
import { AiFillBell, AiFillCamera } from 'react-icons/ai';
import { EP_MODIFICAR_CHATBOT, EP_MODIFICAR_FOTO, EP_MODIFICAR_MIS_DATOS } from "../../../../Constants";
import CameraComponent from "../../../login/components/camera/camera";
import FileUploader from "../../../register/images/FileUploader";
import ModalOpciones from "../foldercontent/components/modal/Modal";

export default class UpdateUser extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            name:"",
            user:"",
            modoBot:"",
            photo:"",
            password:""
        }
        this.refCamera = React.createRef()
        this.refModalCamera = React.createRef()
        this.refFile = React.createRef()
    }

    componentDidMount(){
        this.setState(
            {
                name:localStorage['nombre'],
                user:localStorage['nombreusuario'],
                modoBot:localStorage['modobot'],
                photo:localStorage['foto']
            }
        )

        this.refFile.current.setImageSrc(localStorage['foto'])
    }

    showModalCamera(event){
        event.preventDefault()
        this.refModalCamera.current.setModal("","")
        this.refModalCamera.current.handleShow()
    }

    takePhoto(event){
        event.preventDefault()
        const strImg = this.refCamera.current.takePhoto(event)
        this.setState({ photo:strImg })
        this.refFile.current.setImageSrc(strImg)
        this.refModalCamera.current.handleClose()
    }

    modificar(event){
        event.preventDefault()
        fetch(
            EP_MODIFICAR_MIS_DATOS,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        NoUsuario: localStorage['usuario'],
                        UserName: this.state.user,
                        Nombre: this.state.name,
                        Password: this.state.password
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
                console.log(res);
            }
        )

        fetch(
            EP_MODIFICAR_FOTO,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        NoUsuario: localStorage['usuario'],
                        UserName: this.state.user,
                        Foto: this.state.photo,
                        Password: this.state.password
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
                console.log(res);
            }
        )

        fetch(
            EP_MODIFICAR_CHATBOT,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        NoUsuario: localStorage['usuario'],
                        UserName: this.state.user,
                        ModoBot: this.state.modoBot,
                        Password: this.state.password
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
                console.log(res);
            }
        )
    }

    render(){
        return (
            <Container>
                <Row>
                    <h2>Editar mi información</h2>
                </Row>
                <Row className="justify-content-center">
                    <Col md="6" xs="10">
                        <Form className="g-3 row">
                            <FormGroup className="mb-3">
                                <Form.Label htmlFor="name" >Nombre completo</Form.Label>
                                <Form.Control
                                    className="col-auto mb-2"
                                    type="text"
                                    id="name"
                                    value={this.state.name}
                                    onChange={(e)=> this.setState({name:e.target.value})}
                                >
                                </Form.Control>
                            </FormGroup>
                            <FormGroup className="mb-3">
                                <Form.Label htmlFor="usuario">Usuario</Form.Label>
                                <Form.Control
                                    type="text"
                                    id="usuario"
                                    value={this.state.user}
                                    onChange={(e) => this.setState({user:e.target.value})}
                                ></Form.Control>
                            </FormGroup>
                            <Form.Group className="bm-3">
                                <Form.Label>Cambiar foto de perfil</Form.Label>
                                <Row>
                                    <Col xs={10} md={10}>
                                        <FileUploader ref={this.refFile} ></FileUploader>
                                    </Col>
                                    <Col xs={2} md={2}>
                                        <Button onClick={this.showModalCamera.bind(this)} ><AiFillCamera></AiFillCamera></Button>
                                    </Col>
                                </Row>
                                <Row className="justify-content-center">
                                    <Col md="4">
                                        {/* <Image
                                            width="75em" height="75em" 
                                            src={this.state.photo}
                                        /> */}
                                    </Col>
                                </Row>
                            </Form.Group>
                            <Form.Group className="mb-3">
                                <Form.Label>Modo del bot para el chat</Form.Label>
                                <Row>
                                    <Col>
                                        <Form.Check type="radio" 
                                            label="desactivado" 
                                            id="r1" 
                                            name="g1" 
                                            checked={this.state.modoBot == 0 ? true : false}
                                            onChange={()=>this.setState({modoBot:0})}
                                        />
                                    </Col>
                                    <Col>
                                        <Form.Check type="radio" 
                                            label="activado" 
                                            id="r2" name="g1" 
                                            checked={this.state.modoBot == 0 ? false : true}
                                            onChange={()=>this.setState({modoBot:1})}
                                        />
                                    </Col>
                                </Row>
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Confirmar contraseña</Form.Label>
                                <Row className="justify-content-center">
                                    <Col sm="10" xs="10" className="mb-2">
                                        <Form.Control type="password"
                                            value={this.state.password}
                                            onChange={e => this.setState({password:e.target.value})}
                                        ></Form.Control>
                                    </Col>
                                    <Col sm="2" xs="10" className="mb-2">
                                        <Button onClick={this.modificar.bind(this)}>Modificar</Button>
                                    </Col>
                                </Row>
                            </Form.Group>
                        </Form>
                    </Col>
                </Row>
                <Row>
                    <ModalOpciones size="md" ref={this.refModalCamera}>
                        <CameraComponent ref={this.refCamera}/>
                        <Row>
                            <Col></Col>
                            <Col>
                                <br/>
                                <div className="d-grid">
                                <Button onClick={this.takePhoto.bind(this)} size="lg" >Tomar fotografía</Button>
                                </div>
                            </Col>
                            <Col></Col>
                        </Row>
                    </ModalOpciones>
                </Row>
            </Container>
        )
    }
}