import React from 'react'
import { Col, Container, Form, FormControl, Row } from 'react-bootstrap'
import { EP_VER_CHATS } from '../../../../Constants'
import Friend from '../friends/Fiend'

export default class ChatList extends React.Component {
    constructor(props){
        super(props)
        this.state ={
            amigos:[],
            buscarAmigos:''
        }
    }

    componentDidMount(){
        this.getChats()
    }

    getChats(){
        fetch(
            EP_VER_CHATS,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UsuarioYo:localStorage['usuario']
                    }
                )
            }
        )
        .then(res => res.json()).then(
            res=>
            {
                console.log(res);
                if (res.Error ===0 ){
                    this.setState({amigos:res.Chats})
                }
            }
        )
    }

    render(){
        return(
            <Container>
                <Row>
                    <h2>Chatea con tus amigos</h2>
                </Row>
                <Row className="justify-content-center">
                    <Col sm="8" md="8">
                        <Row>
                            <Col>
                                <h2>Mis amigos</h2>
                            </Col>
                            <Col>
                                <Form className="d-flex">
                                    <FormControl
                                        type="search"
                                        placeholder="Buscar amigo"
                                        className="mr-2"
                                        aria-label="Search"
                                        value={this.state.buscarAmigos}
                                        onChange={e => this.setState({buscarAmigos:e.target.value})}
                                    />
                                </Form>
                            </Col>
                        </Row>
                        <Row>
                            
                        </Row>
                        <Row>
                            {
                                this.state.amigos
                                .filter(a => a.NombreUsuario2.includes(this.state.buscarAmigos))
                                .map(
                                    (o, index)=>{
                                        //console.log(o.UserName);
                                        return <Friend 
                                            key={index}
                                            nousuario={o.NoUsuario2}   
                                            nombre={o.NombreUsuario2}
                                            usuario={o.UserNameUsuario2}
                                            foto={o.FotoUsuario2}
                                            modobot={o.ModoBot}
                                            accion="Chat"
                                        />
                                    }
                                )
                            }
                        </Row>
                    </Col>
                </Row>
            </Container>
        )
    }
}