import React from "react";
import {Card,Button, Col, Row} from 'react-bootstrap'
import { AiFillMinusCircle, AiFillPlusCircle } from "react-icons/ai";
import { EP_ACEPTAR_AMISTAD, EP_ANADIR_AMIGO, EP_RECHAZAR_AMISTAD, IP_S3 } from "../../../../Constants";
import Chat from "../chat/Chat";
import ModalOpciones from "../foldercontent/components/modal/Modal";
class Friend extends React.Component{
    constructor(props){
        super(props)
        this.state={
            nombre:props.nombre,
            nousuario:props.nousuario,
            urlFoto: props.foto[0] === 'h'? props.foto: `${IP_S3}/${props.foto}`,
            modobot:props.modobot || 0,
            accion:props.accion
        }

        this.refChat = React.createRef()
    }

    componentDidMount(){
        
    }

    acction(event){
        event.preventDefault()

        if (this.state.accion === "Agregar amigo"){
            console.log(localStorage['usuario'], this.state.nousuario);
            this.addFriend()
        }else if (this.state.accion =="Chat") {
            this.showChat(event)
        }

    }

    showChat(event){
        event.preventDefault()
        this.refChat.current.showModal(event)
    }

    addFriend(){
        fetch(
            `${EP_ANADIR_AMIGO}`,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UsuarioYo:localStorage['usuario'],
                        UsuarioAmigo:this.state.nousuario
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
                console.log(res);
                this.props.onUpdate(this.state)
            }
        )
    }

    aceptFriend(){
        fetch(
            EP_ACEPTAR_AMISTAD,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UsuarioYo:localStorage['usuario'],
                        UsuarioAmigo:this.state.nousuario
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
            }
        )
    }
    
    deniFriend(){
        fetch(
            EP_RECHAZAR_AMISTAD,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UsuarioYo:localStorage['usuario'],
                        UsuarioAmigo:this.state.nousuario
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                alert(res.Mensaje)
            }
        )
    }

    buttons () {
        return this.state.accion === "Aceptar|Rechazar" ?
        <Row>
            <Col>
                <Button variant="primary" onClick={this.aceptFriend.bind(this)}><AiFillPlusCircle/></Button> 
            </Col>
            <Col>
                <Button variant="primary" onClick={this.deniFriend.bind(this)}><AiFillMinusCircle/></Button>
            </Col>
        </Row>:
        <Button variant="primary" onClick={this.acction.bind(this)}>{this.state.accion}</Button>
    }

    render(){
        return (
            <Col className="mb-3">
                <Card style={{ width: '12rem' }}>
                    <Card.Img  src={this.state.urlFoto} width="auto" height="200px" />
                    <Card.Body>
                        <Card.Title>{this.state.nombre.split(' ')[0]}</Card.Title>
                        <Card.Text>
                            Hola, soy {this.state.nombre}. Ven, seamos amigos!
                        </Card.Text>
                        {
                            this.buttons()
                        }
                    </Card.Body>
                </Card>
                <Row>
                    <Chat ref={this.refChat}
                        NoUsuario={this.state.nousuario}
                        Nombre={this.state.nombre}
                        Foto={this.state.urlFoto}
                        ModoBot={this.state.modobot}
                    ></Chat>
                </Row>
            </Col>
        )
    }
}

export default Friend