import React from "react";
import { Col, Container, ListGroup, Row, Button, Form, Card } from "react-bootstrap";
import { EP_PUBLICAR } from "../../../../Constants";
import FileUploader from "../../../register/images/FileUploader";
export default class Publisher extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            Texto: ""
        }
        this.refImagen=React.createRef()
    }

    componentDidUpdate(){
        console.log('componenete actualizado');
    }

    translate(event){
        event.preventDefault()
        this.setState({Traduccion:'Contenido de la publicación, puede ser cualquier tipo de texto, sin importar el tamaño sin importar las palabras o el idioma.'})

    }

    publish(event){
        event.preventDefault()
        fetch(
            EP_PUBLICAR,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        Texto:this.state.Texto,
                        Imagen:this.refImagen.current.getImageSrc(),
                        Usuario:localStorage['usuario']
                    }
                )
            }
        )
        .then(res => res.json()).then(
            res=>
            {
                alert(res.Mensaje)
                if(res.Error === 0){
                    this.setState({Texto: ""})
                    this.refImagen.current.setImageSrc("")
                    this.props.onUpdate(this.state)
                    
                }
            }
        )
    }


    render(){
        return (
            <Container className="mb-3 mt-4">
                <Row className="justify-content-center">
                    <Col md="8" sm="10">
                        <Row>
                            <h2>Escribe algo para tus amigos</h2>
                        </Row>
                        <Form className="justify-content-start">
                            <Form.Control
                                className="mb-3"
                                as="textarea"
                                placeholder="¿Qué estás pensando?"
                                id="publicar" name="publicar"
                                maxLength="255"
                                value={this.state.Texto}
                                onChange={e => this.setState({Texto:e.target.value})}
                            ></Form.Control>
                            <Row className="justify-content-end">
                                <Col md="8" sm="8">
                                    <FileUploader ref={this.refImagen} ></FileUploader>
                                </Col>
                                <Col md="4" sm="4">
                                    <Button className="btn btn-pimary" onClick={this.publish.bind(this)}>Publicar</Button>
                                </Col>
                            </Row>
                        </Form>
                    </Col>
                </Row>
            </Container>
        )
    }
}