import React from 'react'
import {Modal, Button, Container, Form, Row} from 'react-bootstrap'
import { IP_S3 } from '../../../../../../../Constants';

class View extends React.Component {

    constructor(props)
    {
        super(props)
        this.rutaArchivo = `${IP_S3}/${props.unicName}`
    }

    render() {
        return (
            <Container fluid="xxl">
                <Row>
                    <frameset>
                        <frame name = "middle" 
                        src ={this.rutaArchivo}
                        scrolling="auto"/>
                    </frameset>
                </Row>
            </Container>
        );
    }
}

export default View