import React from "react";
import { Container, Row,Col, Form, FormControl, Button } from "react-bootstrap";
import "./Mensaje.css"

class Mensaje extends React.Component {

    constructor(props){
        super(props)
        this.state={
            Mensaje:"",
            Foto:"",
            NoUsuario:"",
            NoMensaje:"",
            NombreDelUsuario:"",
            Usuario:""
        }
    }

    componentDidMount(){
        this.setState(
            {
                Mensaje:this.props.Mensaje,
                Foto:this.props.Foto,
                NoUsuario:this.props.NoUsuario,
                NoMensaje:"",
                NombreDelUsuario:"",
                Usuario:""
            }
        )
    }

    getMensaje(){
        //console.log(this.state.NoUsuario,localStorage['usuario'] );
        return this.state.NoUsuario != localStorage['usuario'] ? 
        <div class="d-flex flex-row p-3"> 
            <img class="img-mensaje" src={this.state.Foto} width="30" height="30"/>
            <div class="chat ml-2 p-3">{this.state.Mensaje}</div>
        </div>:
        <div class="d-flex flex-row p-3 justify-content-end">
            <div class="bg-white mr-2 p-3"><span class="text-muted">{this.state.Mensaje}</span></div> 
            <img class="img-mensaje" src={localStorage['foto']} width="30" height="30"/>
        </div>
    }

    render(){
        //"https://img.icons8.com/color/48/000000/circled-user-female-skin-type-7.png"
        return this.getMensaje()
    }
}

export default Mensaje