import React from "react";
import { Container, Row,Col, Form, FormControl, Button } from "react-bootstrap";
import { EP_MIS_AMIGOS, EP_USUARIOS, EP_USUARIOS_LISTA } from "../../../../Constants";
import Friend from "./Fiend";

class Friends extends React.Component {

    constructor(props){
        super(props)
        this.state={
            amigos:[],
            buscarAmigos:""
        }
        //this.cargarTodosUsuarios()
    }

    componentDidMount(){
        this.getFriends()
    }

    async getFriends(){

        fetch(
            `${EP_MIS_AMIGOS}`, // EP
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        NoUsuario:localStorage['usuario']
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                if (res.Error !== 0){
                    alert('No se puedieron recuperar tus amigos')
                    return
                }

                this.setState({amigos:res.Gente})
            }
        )
    }

    render(){
        return (
            <Container>
                <Row className="mb-3 justify-content-center">
                    <Col sm="8" md="8">
                        <Row>
                            <Col>
                                <h2>Mis amigos</h2>
                            </Col>
                            <Col>
                                <Form className="d-flex">
                                    <FormControl
                                        type="search"
                                        placeholder="Buscar amigo"
                                        className="mr-2"
                                        aria-label="Search"
                                        value={this.state.buscarAmigos}
                                        onChange={e => this.setState({buscarAmigos:e.target.value})}
                                    />
                                </Form>
                            </Col>
                        </Row>
                        <Row>
                            {
                                this.state.amigos
                                .filter(a => a.Nombre.includes(this.state.buscarAmigos))
                                .map(
                                    (o, index)=>{
                                        //console.log(o.UserName);
                                        return <Friend 
                                            key={index}
                                            nousuario={o.NoUsuario}   
                                            nombre={o.Nombre}
                                            usuario={o.UserName}
                                            foto={o.Foto}
                                            modobot={o.ModoBot}
                                            accion="Chat"
                                        />
                                    }
                                )
                            }
                        </Row>
                    </Col>
                </Row>
            </Container>
        )
    }
}

export default Friends