import React from 'react'
import {Modal, Button, Container, Col, Row, Form} from 'react-bootstrap'
import { EP_EDITAR_ARCHIVO, EP_ELIMINAR_ARCHIVO, EP_LOGIN } from '../../../../../../../Constants';
import Encriptador from '../../../../../../Cripter';

class Delete extends React.Component {

    constructor(props)
    {
        super(props)
        this.state={
            contrasena:'',
            id:props.id,
            fileName:props.fileName
        }
        this.ecriptador = new Encriptador()
    }

    eliminar(event){
        event.preventDefault()

        fetch(
            `${EP_LOGIN}`,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UserName: localStorage['usuario'],
                        Password:this.ecriptador.encriptar(this.state.contrasena)
                    }
                )
            }
        ).then(res=>res.json()).then(
            res => {
                // si hay error, salir
                if (res.Loged !== 1) {
                    alert('Contraseña incorrecta')
                    return
                }

                //contraseña correcta, cargar archivo
                fetch(
                    `${EP_ELIMINAR_ARCHIVO}`,
                    {
                        method:'POST',
                        mode:'cors',
                        headers:{'Content-Type':'application/json'},
                        body:JSON.stringify(
                            {
                                Archivo: this.state.id
                            }
                        )
                    }
                ).then(res => res.json()).then(
                    res => {
                        alert(res.Mensaje)
                    }
                )
            }
        );
    }

    render() {
        return (
            <Container>
                <Row>
                    <Col>
                        <ul className="list-group list-group-flush">
                            <li className="list-group-item">
                                <Form onSubmit={this.eliminar.bind(this)}>
                                    <Form.Group>
                                        <Form.Label as="thead">
                                            Confirma tu contraseña para eliminar el archivo: <strong>{this.state.fileName}</strong>
                                        </Form.Label>
                                        <Form.Control 
                                            required type="password" className="form-control" id="exampleInputPassword1"
                                            value={this.state.contrasena}
                                            onChange={e=> this.setState({contrasena:e.target.value})}
                                        />
                                    </Form.Group>
                                    <br/>
                                    <Form.Group className="d-grid">
                                        <Button variant="primary" size="lg" type="submit">
                                            Eliminar archivo
                                        </Button>
                                    </Form.Group>
                                </Form>
                            </li>
                        </ul>
                    </Col>
                </Row>
            </Container>
        );
    }
}

export default Delete