import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { SidebarData } from './SidebarData';
import './Navbar.css';
import * as FaIcons from 'react-icons/fa';
import * as AiIcons from 'react-icons/ai';
import { IconContext } from 'react-icons';
import ModalOpciones from '../foldercontent/components/modal/Modal';
import UpFile from '../upfile/Upfile';
import { Image } from 'react-bootstrap';
import { PATH_FOTO_PERFIL } from '../../../../Constants';
import { BiExit } from 'react-icons/bi';

class Navbar extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      sidebar:false
    }
  }

  showSidebar(){
    this.setState({sidebar:!this.state.sidebar})
  }


  render(){
    return (
      <>
        <IconContext.Provider value={{ color: '#fff' }}>
          <div className=''>
            <Link to='#' className='menu-bars'>
              <FaIcons.FaBars onClick={this.showSidebar.bind(this)} />
            </Link>
          </div>
          <nav className={this.state.sidebar ? 'nav-menu active' : 'nav-menu'}>
            <ul className='nav-menu-items' onClick={this.showSidebar.bind(this)}>
              <li className='navbar-toggle'>
                <Link to='#' className='menu-bars'>
                <AiIcons.AiOutlineClose />
                </Link>
              </li>
              <li className='navbar-toggle justify-content-center'>
                <Image
                  width="75em" height="75em" 
                  src={localStorage['foto']}
                />
              </li>
              {SidebarData.map((item, index) => {
                return (
                  <li key={index} className={item.cName}>
                    <Link to={item.path} >
                      {item.icon}
                      <span>{item.title}</span>
                    </Link>
                  </li>
                );
              })}
              <li className='nav-text'>
                <Link to="/" 
                  onClickCapture={()=>localStorage['usuario']=''}  
                >
                  <BiExit/>
                  <span>Cerrar sesión</span>
                </Link>
              </li>
            </ul>
          </nav>
        </IconContext.Provider>
      </>
    )
  }
}


export default Navbar;