import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css'
import { Container, Row, Col, Card, Form, Button, FormControl } from "react-bootstrap";
import FileComponent from "./components/file/File";
import { EP_MIS_ARCHIVOS, EP_MIS_ARCHIVOS_PRIVADOS, EP_MIS_ARCHIVOS_PUBLICOS } from "../../../../Constants";

class Archivo {
    constructor(nombre= "", extension="", id=0){
        this.nombre = nombre
        this.extension = extension
        this.id = id
    }
}

class FolderContent extends React.Component {
    
    constructor(props){
        super(props)
        this.state ={
            archivosPrivados:[],
            archivosPublicos:[],
            cadenaBuscar:""
        }
        //this.cargarArchivos()
    }

    cargarArchivos(){
        // se encarga de solicitar la lista de archivos del usuario
        fetch(
            `${EP_MIS_ARCHIVOS_PRIVADOS}`,
            {
                method:`POST`,
                mode:`cors`,
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UserName:localStorage["usuario"]
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                if (res.Error !== 0){
                    // hubo error
                    alert(res.Mensaje)
                    return
                }

                // no hubo error
                // copiar lista de archivos
                this.setState({archivosPrivados:res.Contenido})
            }
        )

        fetch(
            `${EP_MIS_ARCHIVOS_PUBLICOS}`,
            {
                method:`POST`,
                mode:`cors`,
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        UserName:localStorage["usuario"]
                    }
                )
            }
        ).then(res => res.json()).then(
            res => {
                if (res.Error !== 0){
                    // hubo error
                    alert(res.Mensaje)
                    return
                }

                // no hubo error
                // copiar lista de archivos
                this.setState({archivosPublicos:res.Contenido})
            }
        )
    }

    render(){
        return (
            <div>
                <Container style={{ border:"4px", borderBlockColor:"black" }}>
                    <Row>
                        <Col>
                            <h2>Mis archivos</h2>
                        </Col>
                        <Col>
                            <Form className="d-flex">
                                <FormControl
                                    type="search"
                                    placeholder="Nombre de archivo"
                                    className="mr-2"
                                    aria-label="Search"
                                    value={this.state.cadenaBuscar}
                                    onChange={e => this.setState({cadenaBuscar:e.target.value})}
                                />
                            </Form>
                        </Col>
                    </Row>
                    <br></br>
                    <Row>
                        <Col>
                            <h5>archivos privados</h5>
                        </Col>
                    </Row>
                    <Row>
                        {
                            this.state.archivosPrivados
                            .filter(f => f.Nombre.includes(this.state.cadenaBuscar))
                            .map(
                                o => <FileComponent key={o.Id_Documento} fileName={o.Nombre} idf={o.Id_Documento}
                                format={o.Tipo} unicName={o.UnicName} visible={false}
                                editable={true}/>
                            )
                        }
                    </Row>
                    <br></br>
                    <Row>
                        <Col>
                            <h5>archivos públicos</h5>
                        </Col>
                    </Row>
                    <Row>
                        {
                            this.state.archivosPublicos
                            .filter(f => f.Nombre.includes(this.state.cadenaBuscar))
                            .map(
                                o => <FileComponent key={o.Id_Documento} fileName={o.Nombre} idf={o.Id_Documento}
                                format={o.Tipo} unicName={o.UnicName} visible={true}
                                editable={true}/>
                            )
                        }
                    </Row>
                </Container>
            </div>
        );
    }
}

export default FolderContent;