import React from "react";
import { Row, Card } from "react-bootstrap";
import FileComponent from "../foldercontent/components/file/File";

class FriendFiles extends React.Component {
    constructor (props){
        super(props)
        this.state = {
            usuario:props.usuario,
            nombre:props.nombre,
            archivos:props.archivos,
            buscarArchivos:props.buscarArchivos
        }
        //console.log(this.state.archivos);
    }

    render(){
        return (
            <>
                <Card.Header>
                    <h5>{this.state.usuario} : {this.state.nombre}</h5>
                </Card.Header>
                <Card.Body>
                    <Row>
                        {
                            this.state.archivos
                            .map(
                                a => {
                                    return <FileComponent key={a.UnicName} fileName={a.Nombre} idf={a.Id_Documento}
                                        format={a.Tipo} unicName={a.UnicName} visible={false}
                                        editable={false}
                                    />
                                }
                            )
                        }
                    </Row>
                </Card.Body>
            </>
        )
    }
}

export default FriendFiles