import React from "react";
import { Col, Container, ListGroup, Row, Button, Form, Card } from "react-bootstrap";
import ModalOpciones from "../foldercontent/components/modal/Modal";
import {AiOutlineReload, AiOutlineSend } from "react-icons/ai"
import { EP_TRADUCIR } from "../../../../Constants";
import "./Publication.css"

export default class Publication extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            NoPublicacion: "",
            Texto: "", Traduccion:"",
            Imagen: "", 
            Etiquetas: "",
            Fecha: "",
            NoUsuario: "",
            Nombre: "",
            Foto: ""
        }
    }

    componentDidMount(){
        this.setState(
            {
                NoPublicacion: this.props.NoPublicacion,
                Texto: this.props.Texto, Traduccion:"",
                Imagen: this.props.Imagen, 
                Etiquetas: this.props.Etiquetas,
                Fecha: this.props.Fecha,
                NoUsuario: this.props.NoUsuario,
                Nombre: this.props.Nombre,
                Foto: this.props.Foto
            }
        )
    }

    translate(event){
        event.preventDefault()
        fetch(
            EP_TRADUCIR,
            {
                method:'POST',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify(
                    {
                        Texto:this.state.Texto
                    }
                )
            }
        )
        .then(res => res.json()).then(
            res=>
            {
                this.setState({Traduccion:res.Texto})
            }
        )
    }

    render(){
        return (
            <Container className="mb-5">
                <Row className="justify-content-center">
                    <Col md="6" sm="8">
                        <Card>
                            <Card.Header>
                                {/* <Row> */}
                                    <Card.Img src={this.state.Foto} id="foto-perfil-publicacion"/>
                                    <strong className="header-text">
                                        {this.state.Nombre}
                                    </strong>
                                {/* </Row> */}
                            </Card.Header>
                            <Card.Body>
                                <Card.Text>
                                    {this.state.Texto}
                                </Card.Text>
                                <Row className="tex-left"><Card.Link onClick={this.translate.bind(this)}>Traducir</Card.Link></Row>
                                <Card.Text>
                                    <i>{this.state.Traduccion}</i>
                                </Card.Text>
                                {
                                    this.state.Etiquetas.split(',').map(
                                        (o,index) => {
                                            return <Card.Link key={index} >#{o} </Card.Link>
                                        }
                                    )
                                }
                                <Card.Img variant="bottom" src={this.state.Imagen}></Card.Img>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Container>
        )
    }
}